.center-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 100vh;
  margin: 0;
  padding: 0;
}

.page-body {
  font-family: "Roboto Condensed Light", "Arial", sans-serif;
  font-size: 16px;
  line-height: 22px;
  color: rgb(16, 38, 84);
  height: 100%;
}

.page-main {
  flex: 1;
  margin: 0;
  padding: 0;
}

.main-block {
  background: conic-gradient(from 130.31deg at 16.03% 39.06%, #b80027 -46.69deg, #003d85 32.24deg, #b80027 313.31deg, #003d85 392.24deg);
  margin-bottom: 120px;
  padding: 30px 45px 38px;
}
@media (max-width: 1023px) {
  .main-block {
    padding: 38px 45px 77px;
    margin-bottom: 100px;
  }
}
@media (max-width: 767px) {
  .main-block {
    padding: 17px 15px 83px;
    margin-bottom: 67px;
  }
}

.main-block__inner {
  padding: 30px 45px 0;
}

.main-block__header {
  display: flex;
  justify-content: space-between;
  max-width: 1163px;
  margin: 0 auto;
}

.main-block__header p {
  max-width: 175px;
  padding: 0 4px 0 0;
  margin: 0;
}
@media (max-width: 1023px) {
  .main-block__header p {
    padding: 0;
    margin-bottom: 18px;
  }
}

.main-block__header a {
  max-width: 100px;
}

@media (max-width: 1023px) {
  .main-block__header svg {
    width: 81px;
  }
}
@media (max-width: 767px) {
  .main-block__header svg {
    width: 67px;
  }
}

.main-block__header a:last-of-type {
  font-family: "Bebas Neue Bold", "Arial", sans-serif;
  font-size: 26px;
  line-height: 26px;
  letter-spacing: 0.03em;
  color: #ffffff;
}
.main-block__header a:last-of-type:hover {
  color: rgb(16, 75, 163);
}
.main-block__header a:last-of-type:active {
  opacity: 0.5;
}
@media (max-width: 1023px) {
  .main-block__header a:last-of-type {
    font-size: 24px;
    line-height: 24px;
  }
}
@media (max-width: 767px) {
  .main-block__header a:last-of-type {
    font-size: 20px;
    line-height: 20px;
  }
}

.main-block__flex-container {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  transform: translateY(-5px);
  max-width: 1187px;
  margin: 0 auto;
}
@media (max-width: 1218px) {
  .main-block__flex-container {
    transform: unset;
  }
}

.main-block__text-block {
  transform: translateX(-11px);
  text-align: right;
  margin-right: 52px;
  padding-top: 60px;
}
@media (max-width: 1023px) {
  .main-block__text-block {
    padding-top: 10px;
    margin-right: 5px;
    transform: translateX(-26px);
  }
}
@media (max-width: 767px) {
  .main-block__text-block {
    transform: translate(-40px, 3px);
    margin: 0 auto;
  }
}

.main-block__text-block-inner {
  display: flex;
  align-items: center;
  justify-content: center;
}

.main-block__text-block-inner ul {
  font-family: "Roboto Condensed Regular", "Arial", sans-serif;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-end;
  font-size: 14px;
  line-height: 21px;
  color: #ffffff;
  transform: translate(37px, 78px);
  max-width: 130px;
  min-width: 120px;
  list-style: none;
  margin: 0;
  padding: 0;
}
@media (max-width: 1023px) {
  .main-block__text-block-inner ul {
    transform: translate(20px, 59px);
  }
}
@media (max-width: 767px) {
  .main-block__text-block-inner ul {
    transform: translate(31px, 52px);
  }
}

.main-block__text-block-inner h1 {
  font-family: "Bebas Neue Bold", "Arial", sans-serif;
  font-size: 109px;
  line-height: 109px;
  color: #ffffff;
  text-transform: uppercase;
  letter-spacing: 0.03em;
  -webkit-text-stroke: 2px #ffffff;
  -webkit-text-fill-color: #ffffff;
  max-width: 300px;
  margin: 0 0 66px;
}
@media (max-width: 1023px) {
  .main-block__text-block-inner h1 {
    font-size: 86px;
    line-height: 86px;
    transform: translateY(-3px);
    margin-bottom: 40px;
  }
}
@media (max-width: 767px) {
  .main-block__text-block-inner h1 {
    font-size: 78px;
    line-height: 78px;
    margin-bottom: 48px;
  }
}

.main-block__text-block-inner span {
  font-family: "Bebas Neue Regular", "Arial", sans-serif;
  font-size: 49px;
  line-height: 59px;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  -webkit-text-stroke: 0;
  display: inline-block;
  vertical-align: middle;
  padding-top: 43px;
  max-width: 130px;
}
@media (max-width: 1023px) {
  .main-block__text-block-inner span {
    font-size: 34px;
    line-height: 41px;
  }
}
@media (max-width: 767px) {
  .main-block__text-block-inner span {
    font-size: 28px;
    line-height: 34px;
  }
}

.main-block__picture {
  margin-right: 189px;
}
@media (max-width: 1270px) {
  .main-block__picture {
    margin-right: calc(100vw - 1100px);
  }
}
@media (max-width: 1120px) {
  .main-block__picture {
    margin-right: 20px;
  }
}
@media (max-width: 1023px) {
  .main-block__picture {
    margin: 0;
  }
}
@media (max-width: 767px) {
  .main-block__picture {
    display: none;
  }
}

.main-block__text-block > p {
  max-width: 234px;
  margin: 0 0 0 auto;
}
@media (max-width: 1023px) {
  .main-block__text-block > p {
    max-width: 183px;
  }
}
@media (max-width: 767px) {
  .main-block__text-block > p {
    max-width: 290px;
  }
}

.main-block__text-block a {
  font-family: "Roboto Condensed Bold", "Arial", sans-serif;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.05em;
  min-height: 57px;
  display: block;
  text-transform: uppercase;
  border: 4px solid #ffffff;
  background-color: transparent;
  color: #ffffff;
  text-align: center;
  padding: 17px;
}
.main-block__text-block a:hover {
  border: 4px solid rgb(230, 19, 4);
  color: rgb(230, 19, 4);
}
.main-block__text-block a:active {
  opacity: 0.5;
}

@media (max-width: 1023px) {
  .main-block__picture img {
    object-fit: cover;
    height: 389px;
  }
}

.main-block__languages {
  align-self: flex-end;
  padding-bottom: 39px;
}
@media (max-width: 1023px) {
  .main-block__languages {
    display: none;
  }
}

.main-block__languages p {
  margin: 0 0 10px;
}

.main-block__languages a {
  color: #ffffff;
  text-transform: uppercase;
  font-size: 12px;
  line-height: 14px;
}
.main-block__languages a:hover {
  color: rgb(16, 75, 163);
}
.main-block__languages a:active {
  opacity: 0.5;
}

.gym {
  padding: 0 45px;
}
@media (max-width: 1023px) {
  .gym {
    padding-bottom: 16px;
  }
}
@media (max-width: 767px) {
  .gym {
    padding: 0;
    padding-bottom: 70px;
  }
}

.gym__inner {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  max-width: 1163px;
  margin: 0 auto;
}
@media (max-width: 1023px) {
  .gym__inner {
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
  }
}

.gym__text-block {
  max-width: 415px;
  padding: 44px 0 0 60px;
}
@media (max-width: 1023px) {
  .gym__text-block {
    max-width: 502px;
    text-align: center;
    padding: 0;
    margin-bottom: 38px;
  }
}
@media (max-width: 767px) {
  .gym__text-block {
    padding: 0 13px;
    margin-bottom: 35px;
  }
}
@media (max-width: 380px) {
  .gym__text-block {
    text-align: left;
  }
}

.gym__text-block h2 {
  font-family: "Bebas Neue Bold", "Arial", sans-serif;
  font-size: 49px;
  line-height: 49px;
  letter-spacing: 0.04em;
  margin: 0 0 11px;
}
@media (max-width: 1023px) {
  .gym__text-block h2 {
    font-size: 42px;
    line-height: 42px;
    margin: 0 0 9px;
  }
}
@media (max-width: 380px) {
  .gym__text-block h2 {
    font-size: 32px;
    line-height: 32px;
  }
}

.gym__text-block h3 {
  font-family: "Roboto Condensed Regular", "Arial", sans-serif;
  font-size: 18px;
  line-height: 27px;
  margin: 0 0 37px;
}
@media (max-width: 1023px) {
  .gym__text-block h3 {
    margin: 0 0 31px;
  }
}
@media (max-width: 767px) {
  .gym__text-block h3 {
    margin: 0 0 23px;
  }
}

.gym__text-block p {
  line-height: 25px;
  margin: 0;
}
.gym__text-block p:first-of-type {
  margin-bottom: 4px;
}
@media (max-width: 767px) {
  .gym__text-block p:first-of-type {
    line-height: 24px;
    margin: 0;
  }
}
@media (max-width: 320px) {
  .gym__text-block p:first-of-type {
    padding-right: 35px;
  }
}
@media (max-width: 1023px) {
  .gym__text-block p:last-of-type {
    display: none;
  }
}

.gym__picture {
  display: flex;
  width: 660px;
  min-height: 401px;
  background: rgb(208, 209, 220) url("../img/desktop/gym.png") no-repeat;
  background-size: cover;
  z-index: -2;
}
@media (max-width: 1023px) {
  .gym__picture {
    background: rgb(208, 209, 220) url("../img/tablet/gym.png") no-repeat;
    background-size: cover;
    width: 100%;
    min-height: 345px;
  }
}
@media (max-width: 767px) {
  .gym__picture {
    background: rgb(208, 209, 220) url("../img/mobile/gym.png") no-repeat;
    background-size: cover;
    min-height: 313px;
    margin-bottom: 39px;
  }
}

.gym__video-wrapper {
  position: relative;
  top: 0;
  left: 0;
  width: 364px;
  height: 228px;
  text-align: center;
  transform: translate(40px, -113px);
  margin: 0 auto;
}
.gym__video-wrapper::before {
  content: "ddd";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100px;
  height: 100px;
  cursor: pointer;
  background: url("../img/gym-elipse.png") no-repeat;
  background-size: 100px 100px;
}
@media (max-width: 1023px) {
  .gym__video-wrapper::before {
    background-size: 73px;
    width: 73px;
    height: 73px;
  }
}
@media (max-width: 1170px) {
  .gym__video-wrapper {
    left: calc(1163px - 100vw);
  }
}
@media (max-width: 1023px) {
  .gym__video-wrapper {
    left: 0;
    width: 266px;
    height: 167px;
    transform: translate(0, -84px);
  }
}
@media (max-width: 440px) {
  .gym__video-wrapper {
    transform: translateY(calc(221% - 100vw));
  }
}
@media (max-width: 350px) {
  .gym__video-wrapper {
    transform: translate(0);
    width: 100%;
  }
}

.gym__video-wrapper video {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}
@media (max-width: 1023px) {
  .gym__video-wrapper video {
    width: 100%;
    height: 100%;
  }
}
@media (max-width: 350px) {
  .gym__video-wrapper video {
    object-fit: cover;
  }
}

@media (max-width: 767px) {
  .gym__video-wrapper video[data-name=video-desktop] {
    display: none;
  }
}

.gym__video-wrapper video[data-name=video-mobile] {
  display: none;
}
@media (max-width: 767px) {
  .gym__video-wrapper video[data-name=video-mobile] {
    display: flex;
  }
}

.gym__video-button-wrapper button {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 21;
  width: 72px;
  height: 72px;
  border-radius: 50%;
  border: 0;
  background: linear-gradient(44.89deg, #003d85 25.38%, #b80027 74.89%);
  cursor: pointer;
  pointer-events: all;
  padding: 0;
}
.gym__video-button-wrapper button:hover {
  opacity: 0.9;
}
.gym__video-button-wrapper button:active {
  opacity: 0.5;
}
@media (max-width: 1023px) {
  .gym__video-button-wrapper button {
    width: 52px;
    height: 52px;
  }
}

.gym__video-button-wrapper svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 22;
  pointer-events: none;
}
@media (max-width: 1023px) {
  .gym__video-button-wrapper svg {
    width: 12px;
    height: 12px;
  }
}

.passes {
  background: linear-gradient(to bottom, rgb(248, 249, 252) 1%, rgb(248, 249, 252) 88%, #ffffff 88%, #ffffff 100%);
  background-repeat: no-repeat;
  padding: 120px 0 0;
}
@media (max-width: 1023px) {
  .passes {
    background: linear-gradient(to bottom, rgb(248, 249, 252) 1%, rgb(248, 249, 252) 95%, #ffffff 95%, #ffffff 100%);
    background-repeat: no-repeat;
    padding: 97px 0 9px;
