.gym {
  padding: 0 45px;

  @media (max-width: $max-tablet) {
    padding-bottom: 16px;
  }

  @media (max-width: $max-mobile) {
    padding: 0;
    padding-bottom: 70px;
  }
}

.gym__inner {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  max-width: 1163px;
  margin: 0 auto;

  @media (max-width: $max-tablet) {
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
  }
}

.gym__text-block {
  max-width: 415px;
  padding: 44px 0 0 60px;

  @media (max-width: $max-tablet) {
    max-width: 502px;
    text-align: center;
    padding: 0;
    margin-bottom: 38px;
  }

  @media (max-width: $max-mobile) {
    padding: 0 13px;
    margin-bottom: 35px;
  }

  @media (max-width: 380px) {
    text-align: left;
  }
}

.gym__text-block h2 {
  @include bebasneue-bold();
  font-size: 49px;
  line-height: 49px;
  letter-spacing: 0.04em;
  margin: 0 0 11px;

  @media (max-width: $max-tablet) {
    font-size: 42px;
    line-height: 42px;
    margin: 0 0 9px;
  }

  @media (max-width: 380px) {
    font-size: 32px;
    line-height: 32px;
  }
}

.gym__text-block h3 {
  @include robotocondensed-regular();
  font-size: 18px;
  line-height: 27px;
  margin: 0 0 37px;

  @media (max-width: $max-tablet) {
    margin: 0 0 31px;
  }

  @media (max-width: $max-mobile) {
    margin: 0 0 23px;
  }
}

.gym__text-block p {
  line-height: 25px;
  margin: 0;

  &:first-of-type {
    margin-bottom: 4px;

    @media (max-width: $max-mobile) {
      line-height: 24px;
      margin: 0;
    }

    @media (max-width: 320px) {
      padding-right: 35px;
    }
  }

  &:last-of-type {
    @media (max-width: $max-tablet) {
      display: none;
    }
  }
}

.gym__picture {
  display: flex;
  width: 660px;
  min-height: 401px;
  background: $gym-picture url("../img/desktop/gym.png") no-repeat;
  background-size: cover;
  z-index: -2;

  @media (max-width: $max-tablet) {
    background: $gym-picture url("../img/tablet/gym.png") no-repeat;
    background-size: cover;
    width: 100%;
    min-height: 345px;
  }

  @media (max-width: $max-mobile) {
    background: $gym-picture url("../img/mobile/gym.png") no-repeat;
    background-size: cover;
    min-height: 313px;
    margin-bottom: 39px;
  }
}

.gym__video-wrapper {
  position: relative;
  top: 0;
  left: 0;
  width: 364px;
  height: 228px;
  text-align: center;
  transform: translate(40px, -113px);
  margin: 0 auto;

  &::before {
    content: "ddd";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100px;
    height: 100px;
    cursor: pointer;
    background: url("../img/gym-elipse.png") no-repeat;
    background-size: 100px 100px;

    @media (max-width: $max-tablet) {
      background-size: 73px;
      width: 73px;
      height: 73px;
    }
  }

  @media (max-width: 1170px) {
    left: calc((1163px - 100vw));
  }

  @media (max-width: $max-tablet) {
    left: 0;
    width: 266px;
    height: 167px;
    transform: translate(0, -84px);
  }

  @media (max-width: 440px) {
    transform: translateY(calc(221% - 100vw));
  }

  @media (max-width: 350px) {
    transform: translate(0);
    width: 100%;
  }
}

.gym__video-wrapper video {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;

  @media (max-width: $max-tablet) {
    width: 100%;
    height: 100%;
  }

  @media (max-width: 350px) {
    object-fit: cover;
  }
}

.gym__video-wrapper video[data-name="video-desktop"] {
  @media (max-width: $max-mobile) {
    display: none;
  }
}

.gym__video-wrapper video[data-name="video-mobile"] {
  display: none;

  @media (max-width: $max-mobile) {
    display: flex;
  }
}

.gym__video-button-wrapper button {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 21;
  width: 72px;
  height: 72px;
  border-radius: 50%;
  border: 0;
  background: $gym-video-button;
  cursor: pointer;
  pointer-events: all;
  padding: 0;

  &:hover {
    opacity: 0.9;
  }

  &:active {
    opacity: 0.5;
  }

  @media (max-width: $max-tablet) {
    width: 52px;
    height: 52px;
  }
}

.gym__video-button-wrapper svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 22;
  pointer-events: none;

  @media (max-width: $max-tablet) {
    width: 12px;
    height: 12px;
  }
}
